(function($) {
    "use strict";

    $.fn.select2.locales['pl'] = {
        formatNoMatches: function() {
            return "Brak wynikw";
        },
        formatInputTooShort: function(input, min) {
            return "Wpisz co najmniej" + character(min - input.length, "znak", "i");
        },
        formatInputTooLong: function(input, max) {
            return "Wpisana fraza jest za duga o" + character(input.length - max, "znak", "i");
        },
        formatSelectionTooBig: function(limit) {
            return "Moesz zaznaczy najwyej" + character(limit, "element", "y");
        },
        formatLoadMore: function(pageNumber) {
            return "adowanie wynikw";
        },
        formatSearching: function() {
            return "Szukanie";
        }
    };

    $.extend($.fn.select2.defaults, $.fn.select2.locales['pl']);

    function character(n, word, pluralSuffix) {
        //Liczba pojedyncza - brak suffiksu
        //jeden znak
        //jeden element
        var suffix = '';
        if (n > 1 && n < 5) {
            //Liczaba mnoga ilo od 2 do 4 - wasny suffiks
            //Dwa znaki, trzy znaki, cztery znaki.
            //Dwa elementy, trzy elementy, cztery elementy
            suffix = pluralSuffix;
        } else if (n == 0 || n >= 5) {
            //Ilo 0 suffiks w
            //Liczaba mnoga w iloci 5 i wicej - suffiks w (nie poprawny dla wszystkich wyrazw, np. 100 wiadomoci)
            //Zero znakw, Pi znakw, sze znakw, siedem znakw, osiem znakw.
            //Zero elementw Pi elementw, sze elementw, siedem elementw, osiem elementw.
            suffix = 'w';
        }
        return " " + n + " " + word + suffix;
    }
})(jQuery);
