(function ($) {
    "use strict";

     $.fn.select2.locales['az'] = {
        formatMatches: function (matches) { return matches + " ntic mvcuddur, hrkt etdirmk n yuxar v aa dymlrindn istifad edin."; },
        formatNoMatches: function () { return "Ntic taplmad"; },
        formatInputTooShort: function (input, min) { var n = min - input.length; return n + " simvol daxil edin"; },
        formatInputTooLong: function (input, max) { var n = input.length - max; return n + " simvol silin"; },
        formatSelectionTooBig: function (limit) { return "Sadc " + limit + " element se bilrsiniz"; },
        formatLoadMore: function (pageNumber) { return "Daha ox ntic yklnir"; },
        formatSearching: function () { return "Axtarlr"; }
    };

    $.extend($.fn.select2.defaults, $.fn.select2.locales['az']);
})(jQuery);
