(function ($) {
    "use strict";

    $.fn.select2.locales['vi'] = {
        formatNoMatches: function () { return "Khng tm thy kt qu"; },
        formatInputTooShort: function (input, min) { var n = min - input.length; return "Vui lng nhp nhiu hn " + n + " k t"; },
        formatInputTooLong: function (input, max) { var n = input.length - max; return "Vui lng nhp t hn " + n + " k t"; },
        formatSelectionTooBig: function (limit) { return "Ch c th chn c " + limit + " la chn"; },
        formatLoadMore: function (pageNumber) { return "ang ly thm kt qu"; },
        formatSearching: function () { return "ang tm"; }
    };

    $.extend($.fn.select2.defaults, $.fn.select2.locales['vi']);
})(jQuery);

